<template>
  <v-dialog v-model="active" max-width="290">
    <v-card>
      <v-card-title class="headline">{{ title }}</v-card-title>
      <v-card-text>
        {{ message }}
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="green darken-1" flat="flat" @click="onYes()">Yes</v-btn>
        <v-btn color="green darken-1" flat="flat" @click="onNo()">No</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "ConfirmationDialogue",
  props: {
    title: String,
    message: String
  },
  data() {
    return { active: false, resolve: null };
  },
  methods: {
    confirmation() {
      this.active = true;
      return new Promise((resolve, reject) => {
        this.resolve = resolve;
      });
    },
    onYes: function() {
      this.active = false;
      this.resolve(true);
    },
    onNo: function() {
      this.active = false;
      this.resolve(false);
    }
  }
};
</script>
