import Axios from "axios";
import Config from "../../config";
import { getAccessToken } from "../../utils";

const defaultState = {
  lists: []
};
const getters = {
  APPLICATION_LISTS: state => {
    return state.lists;
  }
};
const mutations = {
  SET_APPLICATION_LISTS: (state, payload) => {
    state.lists = payload;
  }
};
const actions = {
  FETCH_APPLICATION_LISTS: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/application_lists", { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    context.commit("SET_APPLICATION_LISTS", data);
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
