import Axios from "axios";
import Config from "../../config";
import { getAccessToken } from "../../utils";

const defaultState = {
  rolesNeeded: {
    a: "user",
    b: "administrator"
  },
  currentUser: {},
  allUsers: []
};
const getters = {
  CURRENT_USER: state => {
    return state.currentUser;
  },
  ALL_USERS: state => {
    return state.allUsers;
  }
};
const mutations = {
  SET_CURRENT_USER: (state, payload) => {
    state.currentUser = payload;
  },
  SET_ALL_USERS: (state, payload) => {
    state.allUsers = payload;
  }
};
const actions = {
  FETCH_ALL_USERS: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/users", { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    context.commit("SET_ALL_USERS", data);
  },
  SET_CURRENT_USER: ({ commit }, payload) => {
    commit("SET_CURRENT_USER", payload);
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
