import Axios from "axios";
import Config from "../../config";
import { getAccessToken } from "../../utils";

const defaultState = {
  physicians: []
};
const getters = {
  PHYSICIAN: state => {
    return state.physicians;
  }
};
const mutations = {
  SET_PHYSICIAN: (state, payload) => {
    state.physicians = payload;
  },
  UPDATE_PHYSICIAN: (state, payload) => {
    const id = state.physicians.findIndex(elem => elem.npi === payload.npi);

    state.physicians[id] = Object.assign({}, payload);
  },
  NEW_PHYSICIAN: (state, payload) => {
    state.physicians.push(payload);
  }
};
const actions = {
  FETCH_PHYSICIAN: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/physician", { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    context.commit("SET_PHYSICIAN", data);
  },
  UPDATE_PHYSICIAN: async ({ commit }, payload) => {
    const res = await Axios.put(Config.apiRoot + "/physician/" + payload.npi, payload, { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    if (!res.data.success) {
      return res.data.message;
    }
    commit("UPDATE_PHYSICIAN", payload);
    return true;
  },
  NEW_PHYSICIAN: async ({ commit }, payload) => {
    const res = await Axios.post(Config.apiRoot + "/physician", payload, { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    if (!res.data.success) {
      return res.data.message;
    }
    commit("NEW_PHYSICIAN", payload);
    return true;
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
