<template>
  <v-app>
    <div v-if="isLoggedIn">
      <v-navigation-drawer
        persistent
        :mini-variant="miniVariant"
        :clipped="clipped"
        v-model="drawer"
        enable-resize-watcher
        fixed
        app
      >
        <v-list>
          <v-list-tile
            value="true"
            v-for="(item, i) in items"
            :key="i"
            v-if="item.role.indexOf(currentUser.role) >= 0"
            @click="routerSelection(item.link)"
          >
            <v-list-tile-action>
              <v-icon v-html="item.icon"></v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title v-text="item.title"></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-navigation-drawer>
      <v-toolbar app :clipped-left="clipped">
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <v-toolbar-title v-text="title"></v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-title v-text="currentUser.fullname"></v-toolbar-title>
        <v-btn flat icon color="red" @click="logout()">
          <i class="material-icons">power_settings_new</i>
        </v-btn>
      </v-toolbar>
      <v-content>
        <router-view/>
      </v-content>
      <v-navigation-drawer temporary :right="right" v-model="rightDrawer" fixed app>
        <v-list>
          <v-list-tile @click="right = !right">
            <v-list-tile-action>
              <v-icon>compare_arrows</v-icon>
            </v-list-tile-action>
            <v-list-tile-title>Switch drawer (click me)</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-navigation-drawer>
      <v-footer :fixed="fixed" app>
        <span>&copy; 2017 v{{ version }}</span>
      </v-footer>
    </div>
    <login v-if="!isLoggedIn" v-on:logonEvent="logonEvent"></login>
    <confirmation-dialog
      ref="confirmationDialog"
      title="Log Out"
      message="Are you sure you want to log out?"
    ></confirmation-dialog>
  </v-app>
</template>

<script>
import Router from "./router";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import { isLoggedIn, logout, getUserFromToken } from "./utils";
import Login from "@/components/Login";

export default {
  name: "App",
  components: {
    ConfirmationDialog,
    Login
  },
  data() {
    return {
      clipped: false,
      drawer: true,
      fixed: false,
      items: [
        {
          icon: "bubble_chart",
          title: "Home",
          link: "home",
          role: ["user", "administrator"]
        },
        {
          icon: "bubble_chart",
          title: "Physician Administration",
          link: "physician-administration",
          role: ["user", "administrator"]
        },
        {
          icon: "bubble_chart",
          title: "Physician Education",
          link: "physician-education",
          role: ["user", "administrator"]
        },
        {
          icon: "bubble_chart",
          title: "Entity Administration",
          link: "entity_administration",
          role: ["administrator"]
        },
        {
          icon: "bubble_chart",
          title: "Practice Administration",
          link: "practice_administration",
          role: ["administrator"]
        },
        {
          icon: "bubble_chart",
          title: "Start Audit",
          link: "audit-record-selection",
          role: ["user", "administrator"]
        },
        {
          icon: "bubble_chart",
          title: "My Audits",
          link: "audit-record-listing",
          role: ["user", "administrator"]
        }
      ],
      dialog: false,
      miniVariant: false,
      right: true,
      rightDrawer: false,
      title: "CHSLI App",
      version: "0.9d",
      isLoggedIn: false
    };
  },
  computed: {
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    }
  },
  methods: {
    routerSelection: function(sel) {
      Router.push({ name: sel });
    },
    logonEvent: function() {
      this.isLoggedIn = isLoggedIn();
      if (this.isLoggedIn) {
        // load data needed from api
        //this.setCurrentUser(this.$store.getters.CURRENT_USER);
        this.$store.dispatch("FETCH_ENTITY");
        this.$store.dispatch("FETCH_PRACTICE");
        this.$store.dispatch("FETCH_PHYSICIAN");
        this.$store.dispatch("FETCH_APPLICATION_LISTS");
        this.$store.dispatch("FETCH_ALL_USERS");
        // token valid, set the current user info
        this.$store.dispatch("SET_CURRENT_USER", getUserFromToken());
      }
    },
    logout: function() {
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          logout();
          this.logonEvent();
        }
      });
    }
  },
  beforeMount() {
    this.logonEvent(); // workaround for logins
  }
};
</script>
