import Axios from "axios";
import Config from "../../config";
import { getAccessToken } from "../../utils";

const defaultState = {
  entity: []
};
const getters = {
  ENTITY: state => {
    return state.entity;
  }
};
const mutations = {
  SET_ENTITY: (state, payload) => {
    state.entity = payload;
  },
  UPDATE_ENTITY: (state, payload) => {
    const id = state.entity.findIndex(elem => elem.id === payload.id);

    state.entity[id] = Object.assign({}, payload);
  },
  NEW_ENTITY: (state, payload) => {
    state.entity.push(payload);
  },
  DELETE_ENTITY: (state, payload) => {
    state.entity = state.entity.filter(elem => elem.id != payload.id);
  }
};
const actions = {
  FETCH_ENTITY: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/entity", { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    context.commit("SET_ENTITY", data);
  },
  UPDATE_ENTITY: async ({ commit }, payload) => {
    const res = await Axios.put(Config.apiRoot + "/entity/" + payload.id, payload, { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    if (!res.data.success) {
      return res.data.message;
    }
    commit("UPDATE_ENTITY", payload);
    return true;
  },
  NEW_ENTITY: async ({ commit }, payload) => {
    const res = await Axios.post(Config.apiRoot + "/entity", payload, { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    if (!res.data.success) {
      return res.data.message;
    }
    payload.id = res.data.new_id;
    commit("NEW_ENTITY", payload);
    return true;
  },
  DELETE_ENTITY: async ({ commit }, payload) => {
    const res = await Axios.delete(Config.apiRoot + "/entity/" + payload.id, { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    if (!res.data.success) {
      return res.data.message;
    }
    commit("DELETE_ENTITY", payload);
    return true;
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
