<template>
  <v-container>
    <v-layout row class="text-xs-center">
      <v-flex xs12 sm8 md6 offset-sm2 offset-md3 class="grey lighten-4">
        <v-container class="text-xs-center">
          <v-alert
            v-model="alert.visible"
            dismissible
            type="error"
          >Login Error. Msg - {{ alert.message }}</v-alert>
          <v-card flat>
            <v-card-title>
              <v-img src="./img/chsli-logo.jpg" height="100" max-width="100" style="margin:auto;"></v-img>
            </v-card-title>
            <v-form>
              <v-text-field
                class="login-field"
                v-model="loginInfo.username"
                prepend-icon="person"
                name="Username"
                label="Username"
              ></v-text-field>
              <v-text-field
                class="login-field"
                v-model="loginInfo.password"
                prepend-icon="lock"
                name="Password"
                label="Password"
                type="password"
                pa-2
              ></v-text-field>
              <v-card-actions>
                <v-btn primary large block @click="login">Login</v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>
 
<script>
import axios from "axios";
import Config from "./../config";
import { login } from "../utils";

export default {
  data: () => ({
    loginInfo: {
      username: "user01",
      password: "test123"
    },
    alert: { visible: false, message: "" }
  }),
  methods: {
    async login() {
      try {
        const res = await axios.post(
          Config.apiRoot + "/authentication/login",
          this.loginInfo
        );
        if (res.data.user) {
          this.$store.dispatch("SET_CURRENT_USER", res.data.user);
          login(res.data.token);
          this.$emit("logonEvent");
        } else {
          this.alert.message = res.data.message.message;
          this.alert.visible = true;
        }
      } catch (e) {
        this.alert = true;
      }
      return true;
    }
  }
};
</script>

<style>
.login-field {
  margin: 8px;
}
</style>
