import Axios from "axios";
import Config from "../../config";
import { getAccessToken } from "../../utils";

// Not working when old token expires and new one is created.  seems to be sending the old one
// Axios.defaults.headers.common["Authorization"] = `Bearer ${getAccessToken()}`;

const defaultState = {
  practice: []
};
const getters = {
  PRACTICE: state => {
    return state.practice;
  }
};
const mutations = {
  SET_PRACTICE: (state, payload) => {
    state.practice = payload;
  },
  UPDATE_PRACTICE: (state, payload) => {
    const id = state.practice.findIndex(elem => elem.id === payload.id);

    state.practice[id] = Object.assign({}, payload);
  },
  NEW_PRACTICE: (state, payload) => {
    state.practice.push(payload);
  },
  DELETE_PRACTICE: (state, payload) => {
    state.practice = state.practice.filter(elem => elem.id != payload.id);
  }
};
const actions = {
  FETCH_PRACTICE: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/practice", { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    context.commit("SET_PRACTICE", data);
  },
  UPDATE_PRACTICE: async ({ commit }, payload) => {
    const res = await Axios.put(Config.apiRoot + "/practice/" + payload.id, payload, { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    if (!res.data.success) {
      return res.data.message;
    }
    commit("UPDATE_PRACTICE", payload);
    return true;
  },
  NEW_PRACTICE: async ({ commit }, payload) => {
    const res = await Axios.post(Config.apiRoot + "/practice", payload, { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    if (!res.data.success) {
      return res.data.message;
    }
    payload.id = res.data.new_id;
    commit("NEW_PRACTICE", payload);
    return true;
  },
  DELETE_PRACTICE: async ({ commit }, payload) => {
    const res = await Axios.delete(Config.apiRoot + "/practice/" + payload.id, { headers: { Authorization: `Bearer ${getAccessToken()}` } });
    if (!res.data.success) {
      return res.data.message;
    }
    commit("DELETE_PRACTICE", payload);
    return true;
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
